<template>
  <div id="login-page">
    <el-form class="login-form" label-position="left" label-width="41px">
      <div class="title">
        铁塔动环监控系统
      </div>
      <el-form-item label="账号">
        <el-input size="mini" v-model="username" type="text" style="width: 180px;"></el-input>
      </el-form-item>
      <el-form-item label="密码">
        <el-input size="mini" id="password" v-model="password" @keydown.enter.native="login" type="password" style="width: 180px;"></el-input>
      </el-form-item>
      <el-form-item label="  ">
        <el-button size="mini" type="primary" @click="login" style="width: 180px">登陆</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: 'login',
  data () {
    return {
      username: '',
      password: ''
    }
  },
  methods: {
    login () {
      this.$api.login({
        username: this.username,
        password: this.password
      }).then(res => {
        sessionStorage.user = JSON.stringify(res.data)
        this.$router.push('/')
      }).catch(e => {

      })
    }
  }
}
</script>

<style lang="less">
#login-page{
  height: 100%;
  padding-top: 200px;
  background-image: url('../assets/image/background.png');
  background-size: 100%;
  .login-form{
    width: 242px;
    margin: auto;
    background-color: #fff;
    padding: 35px 71px 35px 71px;
    .el-form-item{
      margin-bottom: 2px;
    }
    .el-form-item .el-form-item__label{
      color: #000;
    }
    .el-input input{
      border: solid 1px #367ECB;
      background-color: #fff;
      color: #000;
    }
    .el-button{
      margin: 10px 0;
    }
  }
  .title{
    font-size: 20px;
    text-align: center;
    font-weight: bold;
    color: rgba(11,37,76,1);
    line-height: 28px;
    margin-bottom: 34px;
  }
}
</style>



